import Mock from 'mockjs';

// Mock.mock()api
Mock.mock('/api/goodslist', 'get', {
    status: 200,
    message: '',
    'data|5-10': [
        {
            // 'id|+1': 0, //id
            id: '@increment(1)',
            name: '@cword(2,5)',
            price: '@natural(2,10)',
            count: '@natural(100,999)',
            img: '@dataImage(25x25)'
        }
    ]
});

// mockajax--apifunction
Mock.mock('/api/addgoods','post', function(option) {
    // option,
    console.log(option);

    // mock
    return Mock.mock({
        status: 200,
        message: '@cword(2,5)'
    });
});

Mock.mock(/\/api\/goodslist/,'get',function(option) {
    console.log(option);

    // 
    const res = /\/api\/goodslist\/(\d+)/.exec(option.url);

    console.log(res,'res');

    return Mock.mock({
        data: {
            id: res[1] - 0,
            name: '@fruit()',
            price: 2,
            count: 199,
            img: '@dataImage(25x25)'
        },
        status: 200,
        message: ''
    });

});